.Youtube {
	.wrap {
		.title {
			font-size: 120px;
			padding: 30px 60px;
			line-height: 1.1;
		}
	}
	article {
		border-top: 1px solid #aaa;
		padding: 30px 60px;
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;

		h2 {
			width: 50%;
			font-size: 45px;
			line-height: 1.1;
		}
		.pic {
			width: 30%;
			height: 230px;
			background: #000;
			filter: saturate(0);
			transition: 0.5s;

			img {
				width: 100%;
				height: 100%;
				object-fit: cover;
			}

			&:hover {
				filter: saturate(1.2);
			}
		}
		.txt {
			width: 10%;
			display: flex;
			flex-wrap: wrap;
			justify-content: flex-end;

			span {
				font: 18px/1 'arial';
				color: #000;
			}
		}
	}
}
